import React, { Component } from 'react';
import './css/UploadView.css';
import { isImageUrl } from './utils';
import Uploader from './Uploader';
import classNames from 'classnames';
export default class UploadView extends Component {
    constructor() {
        super(...arguments);
        this.componentWillUnmount = () => {
            this.clearTimer();
        };
        this.handleViewTouchStart = (file) => {
            this.timer = setTimeout(() => {
                if (this.props.onPress) {
                    this.props.onPress(file);
                }
            }, 800);
        };
        this.clearTimer = () => {
            clearTimeout(this.timer);
        };
        this.render = () => {
            const { fileList = [], onChange, accept, multiple, onClick } = this.props;
            const uploader = (<Uploader fileList={fileList} onChange={onChange} accept={accept} multiple={multiple}/>);
            const list = fileList.length ? (<div className="upload-view-list">
        {fileList.map(item => {
                const { url, id, name, error } = item;
                if (isImageUrl(url)) {
                    return (<div className={classNames({
                        'upload-item': true,
                        'upload-item-error': error,
                    })} key={`upload-item-${id}`} onTouchStart={() => this.handleViewTouchStart(item)} onTouchMove={this.clearTimer} onTouchEnd={this.clearTimer} onClick={() => onClick && onClick(item)}>
                <img src={url}/>
              </div>);
                }
                else {
                    return (<div className="upload-item" key={`upload-item-${id}`}>
                {name}
              </div>);
                }
            })}
        {uploader}
      </div>) : (uploader);
            return <div className="uploadView">{list}</div>;
        };
    }
}
